import React, { Component } from 'react';
import logo from './logo.png';

export default class DOG extends Component {
  render() {
    return (
      <div style={{ position: 'fixed', top: 20, right: 20, height: 100 }}>
        <img className="DOG" src={logo} />
      </div>
    );
  }
}



